@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lato:wght@100;200;300;400;500&display=swap");
@keyframes fade {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes shrinkY {
  from {
    transform: scaleY(1);
  }
  to {
    transform: scaleY(0);
  }
}
@keyframes grow20 {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.2);
  }
}
@keyframes filterh {
  from {
    filter: invert(96%) sepia(6%) saturate(549%) hue-rotate(348deg) brightness(94%) contrast(87%);
  }
  to {
    filter: invert(47%) sepia(26%) saturate(1181%) hue-rotate(145deg) brightness(107%) contrast(98%);
  }
}
/*Loading*/
.loading-background {
  animation-name: fade;
  animation-duration: 1.2s;
  animation-delay: 0.9s;
}

.loading-logo {
  animation-name: filterh, grow20;
  animation-duration: 0.9s, 0.9s;
  animation-delay: 0s, 0s;
  animation-direction: normal, alternate;
}

/*ProjectsGrid*/
.projectsgrid-description-container.loading {
  animation-name: shrinkY;
  animation-duration: 0.6s;
  animation-direction: alternate;
  animation-iteration-count: 2;
}

.projectsgrid-description-container.loadingfirst {
  animation-name: shrinkY;
  animation-duration: 0.6s;
  animation-direction: reverse;
}

.projectsgrid-description-container {
  transform-origin: top left;
}

.projectsgrid-description-content-container.loading {
  animation-name: fade;
  animation-duration: 0.6s;
  animation-direction: alternate;
  animation-iteration-count: 2;
}

.projectsgrid-description-content-container.loadingfirst {
  animation-name: fade;
  animation-duration: 0.6s;
  animation-direction: reverse;
}

.projectsgrid-description-image.loading {
  animation-name: shrinkY;
  animation-duration: 0.6s;
  animation-direction: alternate;
  animation-iteration-count: 2;
}

.projectsgrid-description-image.loadingfirst {
  animation-name: shrinkY;
  animation-duration: 0.6s;
  animation-direction: reverse;
}

.projectsgrid-description-image {
  transform-origin: top left;
}

/*Slideshow*/
.slideshow-background.loading {
  animation-name: fade;
  animation-duration: 0.6s;
  animation-direction: alternate;
  animation-iteration-count: 2;
}

.slideshow-image.loading {
  animation-name: shrinkY;
  animation-duration: 0.6s;
  animation-direction: alternate;
  animation-iteration-count: 2;
}

.slideshow-image {
  transform-origin: top left;
}

.slideshow-box-container.loading {
  animation-name: shrinkY;
  animation-duration: 0.6s;
  animation-direction: alternate;
  animation-iteration-count: 2;
}

.slideshow-box-container {
  transform-origin: top left;
}

.slideshow-content-container.loading {
  animation-name: fade;
  animation-duration: 0.6s;
  animation-direction: alternate;
  animation-iteration-count: 2;
}

/*Menu*/
.menu-icon {
  transition: transform 0.6s, filter 0.6s;
}

.menu-link {
  transition: transform 0.6s, color 0.6s;
}

/*ProjectGrid*/
.projectsgrid-description-icon-new {
  transition: transform 0.6s, color 0.6s;
}

.projectsgrid-description-icon-github {
  transition: transform 0.6s, filter 0.6s;
}

.projectsgrid-loadmore-button {
  transition: transform 0.6s, color 0.6s, background-color 0.6s;
}

/*ProjectTile*/
.projecttile-container {
  transition: transform 0.6s;
}

/*Slideshow*/
.slideshow-icon-new {
  transition: transform 0.6s, color 0.6s;
}

.slideshow-icon-github {
  transition: transform 0.6s, filter 0.6s;
}

/*About*/
.about-image {
  transition: transform 0.6s;
}

/*Contact*/
.contact-icon {
  transition: transform 0.6s, filter 0.6s;
}

.contact-email {
  transition: transform 0.6s, color 0.6s;
}

/*Introduction*/
.introduction-icon {
  transition: transform 0.6s, color 0.6s;
}

